:root {
  --background: white;
  --default-color: #708090;
  --shadow-color: black;
  --button-text-color: white;
  --button-active-color: #505050;
  --button-disabled-color: #ccc;
  --button-disabled-text-color: #f1f1f1;
  --link-default-color: #666;
  --link-hover-color: #999;
  --link-active-color: red;
  --error-color: red;
  --dark-background: #e6e6fa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 14px;
}

body {
  -webkit-user-select: none;
  user-select: none;
  color: var(--default-color);
  margin: 0;
}

h1 {
  margin: 10px 0 0;
}

h2, h3 {
  margin: 10px 0;
}

button {
  background: var(--default-color);
  color: var(--button-text-color);
  cursor: pointer;
  border: none;
  border-radius: 3px;
  outline: none;
  padding: 5px 10px;
}

button:active {
  background: var(--button-active-color);
}

button:disabled {
  background: var(--button-disabled-color);
  color: var(--button-disabled-text-color);
}

a:link, a:visited {
  color: var(--link-default-color);
}

a:hover {
  color: var(--link-hover-color);
}

a:active {
  color: var(--link-active-color);
}

.wrapper {
  text-align: center;
}

.game {
  text-align: left;
  display: inline-block;
}

.map-params {
  margin: 10px 0;
  overflow: hidden;
}

.help-button {
  float: right;
}

.map-params__item + .map-params__item {
  margin-top: 5px;
}

.controls {
  margin-top: 10px;
}

.delimiter {
  margin: 0 5px;
}

.delimiter:after {
  content: "|";
}

.messages-container {
  position: absolute;
  top: 20px;
  right: 20px;
}

.messages-container .message {
  border: 1px solid var(--default-color);
  color: var(--default-color);
  background: var(--background);
  border-radius: 5px;
  width: 200px;
  margin-bottom: 10px;
  padding: 20px;
  position: relative;
}

.messages-container .close {
  cursor: pointer;
  font-size: 20px;
  position: absolute;
  top: 0;
  right: 5px;
}

.messages-container .error {
  color: var(--error-color);
  border-color: var(--error-color);
}

.classical-version-link {
  display: none;
}

.help {
  --width: 600px;
  --height: 420px;
  cursor: initial;
  -webkit-user-select: text;
  user-select: text;
  width: var(--width);
  height: var(--height);
  top: calc((100vh - var(--height)) / 2);
  left: calc((100vw - var(--width)) / 2);
  background: var(--background);
  border: 1px solid var(--default-color);
  box-shadow: var(--shadow-color) 2px 2px 2px;
  border-radius: 10px;
  padding: 5px 10px;
  position: absolute;
}

.help_hidden {
  display: none;
}

.help__close {
  position: absolute;
  top: 10px;
  right: 10px;
}

